import React from 'react'
import Helmet from 'react-helmet'
import PropTypes from 'prop-types'
import Pricing from '../Pricing'
import PreFooter from "../PreFooter"

const PricingPageTemplate = ({
  title,
  meta_title,
  meta_description,
  pricing,
}) => (
  [<div className="bg-white flex flex-col">
    <div className="container mx-auto px-8">
      <Helmet>
        <title>{meta_title}</title>
        <meta name='description' content={meta_description} />
      </Helmet>

      <main className="flex flex-wrap items-center text-center md:text-left my-12 py-12 lg:px-24">
        <div className="w-full md:w-full whitespace-pre-line">
          <h1 className="text-5xl text-black leading-tight mb-6">
            {title}
          </h1>
        </div>
      </main>

      <h2 className='has-text-weight-semibold is-size-2'>
        {pricing.heading}
      </h2>
      <p className='is-size-5'>{pricing.description}</p>

      <section className='flex flex-wrap mb-4 my-12'>
        <Pricing data={pricing.plans} />
      </section>

      <PreFooter />
    </div>
  </div>,
  <PreFooter />]
)

PricingPageTemplate.propTypes = {
  title: PropTypes.string,
  meta_title: PropTypes.string,
  meta_description: PropTypes.string,
  pricing: PropTypes.shape({
    heading: PropTypes.string,
    description: PropTypes.string,
    plans: PropTypes.array,
  }),
}

export default PricingPageTemplate
